








































import {Component, Vue, Watch} from "vue-property-decorator";
import store from '@/store'
import MetaSetting from "@/plugins/meta"
import {mapGetters} from "vuex";
import {SUPPORTED_LOCALES} from '@/common/locale';
import ZemJam from "@/views/event/_zemJam.vue";
import $ from "@/script/js/jquery-3.6.0";

@Component({
    components: {ZemJam},
})
export default class App extends Vue {

    path = "/";
    locales = SUPPORTED_LOCALES;

    isMobile() {
        return /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent);
    }

    async mounted() {

        await this.$store.dispatch("loginState");

        if(localStorage.getItem('zj-popup-open')){
            localStorage.removeItem('zj-popup-open')
        }

        /**
        * game zem modal
        * */
        if (this.$route.name !== 'zemJam') {
            if (localStorage.getItem('zj-popup-open') !== 'false' || localStorage.getItem('zj-popup-open') === null) {
                this.$modal.show('openEventPopup')
            }
        }

    }

    moveLogin() {
        this.$modal.hide('needLogin')
        this.$router.push(`/${this.$i18n.locale}/login`)
    }


}
